export default "<!--  -->\n<template>\n  <header class=\"pageHeader\">\n    <div class=\"headerLeft\">\n      <div class=\"leftSlot\"><slot name=\"leftSlot\">子标题</slot></div>\n    </div>\n    <div class=\"headerMid\">\n      {{ pagetitle }}\n    </div>\n    <div class=\"headerRight\">\n      <div class=\"rightSlot\"><slot name=\"rightSlot\">子标题</slot></div>\n    </div>\n  </header>\n</template>\n\n<script>\n  export default {\n    props: {\n      pagetitle: {\n        type: String,\n        default: '页面标题'\n      }\n    },\n    data() {\n      return {};\n    }\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .pageHeader {\n    display: flex;\n    justify-content: space-between;\n    min-width: 1300px;\n    height: 70px;\n    background-image: url(data:image/jpeg;base64,);\n    background-repeat: no-repeat;\n    background-size: 100% 100%;\n    .headerLeft {\n      flex: 1;\n      height: 25px;\n      margin-top: 16px;\n      background-image: url(data:image/png;base64,);\n      background-repeat: no-repeat;\n      background-size: 100% 100%;\n      color: #fff;\n      .leftSlot {\n        margin-left: 80px;\n      }\n    }\n    .headerMid {\n      width: 490px;\n      text-align: center;\n      color: #fff;\n      font-size: 28px;\n      line-height: 70px;\n    }\n    .headerRight {\n      flex: 1;\n      display: flex;\n      justify-content: flex-end;\n      height: 25px;\n      margin-top: 16px;\n      background-image: url(data:image/png;base64,);\n      background-repeat: no-repeat;\n      background-size: 100% 100%;\n      color: #fff;\n      .rightSlot {\n        margin-right: 80px;\n      }\n    }\n  }\n</style>\n";